<template>
  <view class="number-box">
	<view class="reduce icon" :class="[color, {'disabled':inputValue-step<minNumber}, shape]" @tap="reduce">
	  <text class="iconfont icon-minus-bold">
		  <text style="font-size: 20px;" color="#000000"> - </text>
		  <!-- <uni-icons type="arrow-down" size="18" color="#000000"></uni-icons> -->
	  </text>
	</view>
	<view class="input-box">
	  <input class="input"  type="number" :value="inputValue" @input="countNumber" :disabled="disabled">
	</view>
	<view class="add icon" :class="[color, {'disabled':inputValue+step>maxNumber}, shape]" @tap="add">
	  <text class="iconfont icon-add-bold">
		  <text style="font-size: 20px;" color="#ffffff"> + </text>
		  <!-- <uni-icons type="arrow-up" size="18" color="#ffffff"></uni-icons> -->
	  </text>
	</view>
  </view>
</template>

<script>
	export default {
		name: 'stepper',
		props: {
		  // 最小值
		  minNumber: {
			type: Number,
			default: 1
		  },
		  // value 是数值
		  value: {
			type: Number,
			default: 1
		  },
		  // 最大值
		  maxNumber: {
			type: Number,
			default: 999
		  },
		  // 文本框是否不可编辑
		  disabled: {
			type: Boolean,
			default: true
		  },
	
		  // 加减方框颜色
		  color: {
			type: String,
			default: 'bg-black'  // 'bg-black（黑）', 'bg-blue（蓝）'
		  },
		  // 加减方框形状
		  shape: {
			type: String,
			default: '' // 空--默认，circle-圆形
		  },
	
		  // 每次加或减的值(整数)
		  step: {
			type: Number,
			default: 1
		  }
	
		},
		data() {
		  return {
			inputValue: this.value
		  }
		},
		watch: {
		  inputValue(number) {
			this.$emit('eventChange', number);
		  }
		},
	
		methods: {
		  // 点击减少
		  reduce() {
			if ((this.inputValue - this.step) < this.minNumber) {
			  return;
			}
			this.inputValue = parseInt(this.inputValue) - this.step;
		  },
	
		  // 点击增加
		  add() {
			if ((this.inputValue + this.step) > this.maxNumber) {
			  return;
			}
	
			this.inputValue = parseInt(this.inputValue) + this.step;
	
		  },
	
		  // 数量校验
	
		  countNumber(e) {
			let value = e.detail.value;
			if (!value) {
			  this.inputValue = this.minNumber;
			  return;
			}
	
			if (value < this.minNumber) {
			  uni.showToast({
				title: '数量不能小于' + this.minNumber,
				icon: 'none',
				duration: 1500
	
			  });
			  this.inputValue = this.minNumber;
			  return;
	
			}
			if (value > this.maxNumber) {
			  uni.showToast({
				title: '数量不能大于' + this.maxNumber,
				icon: 'none',
				duration: 1500
			  });
	
			  this.inputValue = this.maxNumber;
			  return;
			}
			this.inputValue = value;
		  }
		}
	}
</script>

<style lang="less" scoped>

  .number-box {
    display: flex;
    .icon {
      height: 60rpx;
      width: 60rpx;
      line-height: 60rpx;
      text-align: center;
      &.reduce {
        border-radius: 8rpx 0 0 8rpx;
      }
      &.add {
        border-radius: 0 8rpx 8rpx 0;
      }
      &.circle {
        border-radius: 50%;
      }
      &.bg-black {
        background-color: rgb(235, 236, 238);
        color: rgb(50, 50, 51);
        &.disabled {
          color: rgb(200, 201, 204);
          background-color: rgb(247, 248, 250);
        }
      }
      &.bg-blue {
        color: rgb(255, 255, 255);
        background-color: rgb(41, 121, 255);
        &.disabled {
          background-color: rgb(247, 248, 250);
          color: rgb(255, 255, 255);
        }
      }
	  &.bg-orange {
	    color: rgb(255, 255, 255);
	    background-color: rgb(255, 195, 0);
	    &.disabled {
	      background-color: rgb(247, 248, 250);
	      color: rgb(255, 255, 255);
	    }
	  }
    }

    .input-box {
      margin: 0 6rpx;
      text-align: center;
      .input {
        background-color: rgb(235, 236, 238);
        width: 80rpx;
        height: 60rpx;
        font-size: 32rpx;
      }
    }
  }

</style>